/* Small Tablets */ /* Large Tablets and Small Laptops */
@media ((min-width: 749px) and (max-width:1023px)) {
    #register-content {
        /* Register Form */
        background: #FFFFFF;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 22px;
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-rows: auto
    }
    
    #register-header h1 {
        /* Register Title*/
    
        font-family: 'Inter';
        font-style: normal;
        font-weight: 900;
        font-size: 96px;
        line-height: 116px;
        text-align: center;
    
        color: #FF6F77;
        margin: 0;
        padding: 0;
    }
    
    #register-header p {
        /* TaskFlow: A Collaborative Task Management System */
    
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        text-align: center;
    
        color: #565656;
        margin: 0;
        padding: 0;
    }
    
    #register-main {
        display: grid;
        align-items: start;
    }
    
    #register-form {
        display: grid;
        place-items: center center;
        gap: 1.2rem;
    }
    
    #register-form > div{
        margin: auto;
        min-width: 90%;
        max-width: 90%;
        text-align: start;
        text-wrap: wrap;
    }
    
    #register-form input, span{
        font-size: 22px;
    }
    
    #register-form button{
        font-size: 22px;
        width: 40%;
        height: 50px;
    }
    
    #register-footer {
        text-align: center;
    }
    #register-footer a{
        /* Already Registered */
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
    
        color: #FF6E77;
    
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    
    }    
}